import './desktopNav.css'
import LanguageSelector from '../../switch/languageSwitch'
import { Link } from 'react-router-dom'
import { withTranslation } from 'react-i18next'

const DesktopNav = ({ t }) => {
    return (
        <div className="desknav">
            <ul className="navbar">
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/`}>
                        {t('homeNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/service`}>
                        {t('serviceNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/collabs`}>
                        {t('collabsNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/private`} title="Private">
                        {t('privateLabelNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/about`}>
                        {t('aboutNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/career`}>
                        {t('careerNavLink.label')}
                    </Link>
                </li>
                <li>
                    <Link to={`${process.env.PUBLIC_URL}/`}>
                        {t('contactNavLink.label')}
                    </Link>
                </li>
                <li>
                    <LanguageSelector />
                </li>
            </ul>
        </div>
    )
};

export default withTranslation()(DesktopNav)
