import React, { Suspense } from 'react'
import './App.css'
import './i18n'
import Navbar from './components/nav/navbar'
import Home from './pages/home/home'
import Collabs from './pages/collabs/collabs'
import Service from './pages/service/service'
import PrivateLabel from './pages/privateLabel/privateLabel'
import AboutUs from './pages/about/about'
import Careers from './pages/career/career'
import Contact from './pages/contact/contact'
import Play from './pages/play/play'
import { Route, Switch } from 'react-router-dom';

const App = () => {
  return (
    <Suspense fallback={null}>
      <Navbar />
      <Switch>
        <Route exact path={`${process.env.PUBLIC_URL}/`} component={Home} />
        <Route exact path={`${process.env.PUBLIC_URL}/service`} component={Service} />
        <Route exact path={`${process.env.PUBLIC_URL}/collabs`} component={Collabs} />
        <Route exact path={`${process.env.PUBLIC_URL}/privatelabel`} component={PrivateLabel} />
        <Route exact path={`${process.env.PUBLIC_URL}/about`} component={AboutUs} />
        <Route exact path={`${process.env.PUBLIC_URL}/career`} component={Careers} />
        <Route exact path={`${process.env.PUBLIC_URL}/contact`} component={Contact} />
        <Route exact path={`${process.env.PUBLIC_URL}/play`} component={Play} />
      </Switch>
    </Suspense>
  )
}

export default App